// VideoApp Component
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import VideoUpload from './VideoUpload';
import WatchVideo from './WatchVideo';
import MetaHead from '../../../MetaHead';

const VideoApp = () => {
  const [videos, setVideos] = useState([]);

  useEffect(() => {
    fetchVideos();
  }, []);

  const fetchVideos = async () => {
    try {
      const response = await axios.get(`${window.location.origin}/uploadvideo/getVideos`);
      setVideos(response.data.videos);
    } catch (error) {
      console.error('Failed to fetch videos', error);
    }
  };

  const handleUploadSuccess = (newVideo) => {
    setVideos([...videos, newVideo]);
  };

  const handleDelete = async (id) => {
    try {
      await axios.delete(`${window.location.origin}/uploadvideo/deleteVideo/${id}`);
      setVideos(videos.filter((video) => video._id !== id));
    } catch (error) {
      console.error('Failed to delete video', error);
    }
  };

  return (
    <div className='containerr'>
   <MetaHead
  title="Islamic Videos-Quran E Noor "
  description="Watch and explore a wide range of Islamic videos, including Quranic recitations, Islamic lectures, lectures on Hadith, stories of the Prophets, and other educational content to deepen your understanding of Islam."
  keywords="Islamic videos, Quranic recitations, Islamic lectures, Hadith videos, Islamic teachings, Muslim video content, learn about Islam, Islamic documentaries, Islamic video lectures, Islamic stories, Prophets stories"
/>
    <div>
      <VideoUpload onUploadSuccess={handleUploadSuccess} />
      </div>
      <WatchVideo videos={videos} onDelete={handleDelete} />
    </div>
  );
};

export default VideoApp;