// MetaHead.js
import React from 'react';
import { Helmet } from 'react-helmet';

const MetaHead = ({ title, description, keywords, url = "https://tripwaly.com/" }) => (
  <Helmet>
    {/* Primary Meta Tags */}
    <title>{title}</title>
    <meta name="description" content={description} />
    <meta name="keywords" content={keywords} />
    <meta name="robots" content="index, follow" />

    {/* Open Graph / Facebook Meta Tags */}
    <meta property="og:type" content="website" />
    <meta property="og:title" content={title} />
    <meta property="og:description" content={description} />
    <meta property="og:url" content={url} />
    <meta property="og:site_name" content="Quran E Noor" />

    {/* Twitter Meta Tags */}
    <meta name="twitter:card" content="summary" />
    <meta name="twitter:title" content={title} />
    <meta name="twitter:description" content={description} />

    {/* Additional SEO Meta Tags */}
    <link rel="canonical" href={url} />
    <meta name="author" content="Quran E Noor" />
    <meta name="language" content="English" />
    <meta name="rating" content="General" />

    {/* Schema.org for Google */}
    <script type="application/ld+json">
      {`
        {
          "@context": "https://schema.org",
          "@type": "WebSite",
          "name": "Quran E Noor",
          "url": "${url}",
          "description": "${description}",
          "sameAs": [
            "https://www.facebook.com/profile.php?id=61563844558032",
            "www.linkedin.com/in/-mern-stack-developer-101344292",
            "https://www.instagram.com/mudassirdeveloper92/"
          ]
        }
      `}
    </script>
  </Helmet>
);

export default MetaHead;
